.page {
  scroll-behavior: smooth;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__body {
  min-width: 320px;
  max-width: 1280px;
  min-height: 100vh;
  margin: 0 auto;
  overflow-x: hidden;
}

.page__menu {
  pointer-events: none;
  opacity: 0;
  z-index: 1000;
  background-color: #000000bf;
  width: 100vw;
  transition: all .6s;
  position: fixed;
  inset: 0;
  transform: translateY(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateY(0);
}

.container {
  padding-inline: 20px;
}

@media (width >= 640px) {
  .container {
    padding-inline: 34px;
  }
}

@media (width >= 1280px) {
  .container {
    padding-inline: 55px;
  }
}

.overflow-block {
  margin-inline: -20px;
}

@media (width >= 640px) {
  .overflow-block {
    margin-inline: -34px;
  }
}

@media (width >= 1280px) {
  .overflow-block {
    margin-inline: -55px;
  }
}

.icon {
  width: 24px;
  height: 24px;
  transition: transform .6s;
}

.icon:hover {
  transform: scale(1.2);
}

.icon--menu {
  background-image: url("icon-menu.d4b7c3e0.svg");
  margin-top: 18px;
}

@media (width >= 640px) {
  .icon--menu {
    margin-top: 24px;
  }
}

.icon--close {
  background-image: url("icon-close.ee68a0c1.svg");
}

.icon--arrow-light {
  background-image: url("arrow-light.8b3877c7.svg");
}

.icon--facebook {
  background-image: url("icon-facebook.802e1b89.svg");
}

.icon--instagram {
  background-image: url("icon-instagram.fa9233de.svg");
}

.logo {
  background-image: url("logo.9435ae94.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 55px;
  height: 61px;
  transition: transform .6s;
}

.logo:hover {
  transform: scale(1.2);
}

.logo--footer {
  width: 63px;
  height: 72px;
}

.main__content {
  flex-direction: column;
  row-gap: 100px;
  padding-top: 70px;
  display: flex;
}

@media (width >= 640px) {
  .main__content {
    row-gap: 88px;
    padding-top: 88px;
  }
}

@media (width >= 1280px) {
  .main__content {
    row-gap: 200px;
    padding-top: 200px;
  }
}

.current-events {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

@media (width >= 640px) {
  .current-events {
    gap: 48px;
  }
}

@media (width >= 1280px) {
  .current-events {
    gap: 56px;
  }
}

.current-events__events {
  flex-direction: column;
  gap: 60px;
  display: flex;
}

@media (width >= 640px) {
  .current-events__events {
    gap: 64px;
  }
}

@media (width >= 1280px) {
  .current-events__events {
    gap: 104px;
  }
}

.event {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  column-gap: 20px;
  display: grid;
}

@media (width >= 640px) {
  .event {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 1280px) {
  .event {
    --columns: 12;
  }
}

.event__image {
  grid-column: 1 / -1;
  width: 100%;
  transition: transform .6s;
}

.event__image:hover {
  transform: scale(1.05);
}

@media (width >= 1280px) {
  .event__image {
    grid-column: 1 / 8;
  }
}

.event__info {
  grid-column: 1 / -1;
}

@media (width >= 640px) {
  .event__info {
    grid-column: 1 / 5;
  }
}

@media (width >= 1280px) {
  .event__info {
    grid-column: 9 / -1;
  }
}

.event__meta {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

@media (width >= 640px) {
  .event__meta {
    justify-content: start;
    gap: 20px;
    margin-top: 20px;
  }
}

@media (width >= 1280px) {
  .event__meta {
    margin-top: 0;
  }
}

.event__title {
  margin-top: 10px;
  display: inline;
  position: relative;
}

@media (width >= 640px) {
  .event__title {
    margin-top: 30px;
  }
}

.event__title:before {
  content: "";
  background-color: #f14a27;
  width: 10px;
  height: 10px;
  position: absolute;
  top: 50%;
  right: -30px;
  transform: translateY(-50%);
}

@media (width >= 640px) {
  .event__title:before {
    width: 15px;
    height: 15px;
  }
}

.event__description {
  margin-top: 11px;
}

@media (width >= 640px) {
  .event__description {
    margin-top: 15px;
  }
}

.announcement {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.announcement__image {
  width: 100%;
}

.announcement__meta {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.announcement__info {
  box-sizing: border-box;
  background-color: #fff;
  height: 140px;
  margin-inline: 20px;
  padding: 20px;
  transform: translateY(-37px);
  box-shadow: 0 2px #1a5a4c40;
}

@media (width >= 640px) {
  .announcement__info {
    width: 570px;
    height: 204px;
    margin: 0;
    padding: 45px 55px 55px;
    transform: translateY(-84px);
  }
}

@media (width >= 1280px) {
  .announcement__info {
    padding: 45px 100px 50px;
  }
}

.announcement__title {
  font-weight: 400px;
  text-align: center;
  margin-top: 20px;
}

@media (width >= 640px) {
  .announcement__title {
    text-align: left;
    margin-top: 16px;
  }
}

.gallery {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

@media (width >= 640px) {
  .gallery {
    gap: 48px;
  }
}

@media (width >= 1280px) {
  .gallery {
    gap: 56px;
  }
}

.carousel {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.carousel-track {
  grid-template-columns: repeat(4, 100%);
  transition: transform .5s ease-in-out;
  display: grid;
}

@media (width >= 640px) {
  .carousel-track {
    grid-template-columns: repeat(4, 50%);
  }
}

@media (width >= 1280px) {
  .carousel-track {
    --columns: 2;
    grid-template-columns: repeat(var(--columns), 1fr);
    gap: 30px;
    display: grid;
  }
}

@media (width >= 1280px) and (width >= 640px) {
  .carousel-track {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 1280px) and (width >= 1280px) {
  .carousel-track {
    --columns: 12;
  }
}

.carousel-slide {
  box-sizing: border-box;
  width: 100%;
  padding-inline: 8px;
  transition: transform .6s;
}

@media (width >= 640px) {
  .carousel-slide {
    padding-inline: 10px;
  }
}

.carousel-slide:hover {
  transform: scale(1.05);
}

@media (width >= 1280px) {
  .carousel-slide {
    grid-column: span 4;
    padding: 0;
  }

  .carousel-slide:nth-child(2), .carousel-slide:nth-child(3) {
    grid-column: span 8;
  }
}

.carousel img {
  object-fit: cover;
  object-position: top;
  width: 100%;
  height: 410px;
  display: block;
}

@media (width >= 640px) {
  .carousel img {
    height: 340px;
  }
}

@media (width >= 1280px) {
  .carousel img {
    height: 370px;
  }
}

input[type="radio"] {
  display: none;
}

.current-slide {
  opacity: 1;
}

.carousel-track.move-slide-1 {
  transform: translateX(0);
}

.carousel-track.move-slide-2 {
  transform: translateX(-100%);
}

@media (width >= 640px) {
  .carousel-track.move-slide-2 {
    transform: translateX(-50%);
  }
}

.carousel-track.move-slide-3 {
  transform: translateX(-200%);
}

@media (width >= 640px) {
  .carousel-track.move-slide-3 {
    transform: translateX(-100%);
  }
}

.carousel-track.move-slide-4 {
  transform: translateX(-300%);
}

@media (width >= 640px) {
  .carousel-track.move-slide-4 {
    transform: translateX(-150%);
  }
}

.carousel-nav {
  justify-content: center;
  gap: 22px;
  margin-top: 20px;
  display: flex;
}

@media (width >= 640px) {
  .carousel-nav {
    margin-top: 32px;
  }
}

@media (width >= 1280px) {
  .carousel-nav {
    display: none;
  }
}

.carousel-nav button {
  box-sizing: border-box;
  cursor: pointer;
  background: #e0e0e0;
  border: none;
  border-radius: 50%;
  width: 9px;
  height: 9px;
  padding: 0;
}

.carousel-nav button:hover, .carousel-nav button:active {
  background: #1a5a4c;
}

